<template>
	<div id="layout">
		<AsideComp></AsideComp>
    <HeaderComp></HeaderComp>
    <keep-alive>
      <MainComp></MainComp>
    </keep-alive>
	</div>
</template>
<script>
import AsideComp from './Aside'
import HeaderComp from './Header'
import MainComp from './Main'
import { getMyinfo, getPermissionList } from '@/api'
export default {
  name: 'layout',
  components: {
    AsideComp,
    HeaderComp,
    MainComp
  },
  methods: {
    getPermission () {
      let sessionId = localStorage.getItem('sessionId')
      getPermissionList({sessionId: sessionId}).then(res => {
        console.log(res)
        let data = res.data
        this.$store.dispatch('setUserPermission', data.result)
        // this.$store.dispatch('setUserName', data.name)
        // this.$store.dispatch('setPartner', data.partner)
      })
      .catch(error => {
        console.log(error)
      })
    },
    // 获取用户信息
    getUserInfo() {
      getMyinfo().then(res => {
        // console.log(res)
        if(res.data.code === '0001') {
          this.$store.dispatch('saveUserInfo', res.data.result.userInfo)
        } else {
          this.$message.error(res.data.message)
        }
      }).catch(err => {
        console.log(err)
        this.$catchError(err)
      })
    },
  },
  mounted() {
    this.getUserInfo()
  }
}
</script>



